<script>
import { defineComponent, ref, computed } from 'vue';
import { useStore } from 'vuex';

export default defineComponent({
  props: {
    index: Number
  },
  setup(props) {
    const store = useStore();

    // default value
    let selectedDataRowType = ref("1_Basic_1_Boolean");
    // dafault dataRowType
    let selectedDataRowTypeSplit = ref(computed(() => selectedDataRowType.value.split("_")));
    /*
    store.commit("addDataRow", {
      dataRow: {
        categoryId: selectedDataRowTypeSplit.value[0],
        categoryName: selectedDataRowTypeSplit.value[1],
        item: {
          itemId: selectedDataRowTypeSplit.value[2],
          itemName: selectedDataRowTypeSplit.value[3],
          itemKeyName: "",
          itemOption: {}
        }
      }
    });
    */

    // 선택한 data type을 store에 저장
    const setDataRowType = (() => {
      store.commit("setDataRowType", {
        index: props.index,
        dataRowType: selectedDataRowTypeSplit.value[3]
      });
      console.log("dataRowType -->", store.getters.getDataRowType(props.index));
    });

    return {
      selectedDataRowType,
      setDataRowType
    }
  },
});
</script>

